/**
 * Deletion cache manager
 *
 * Stores file content before deletion (PreToolUse) so it can be used
 * to generate diffs after deletion (PostToolUse)
 */

import { mkdir, readFile, readdir, rm, stat, writeFile } from "node:fs/promises";
import { join } from "node:path";
import { DELETION_CACHE_DIR, DELETION_CACHE_TTL_MS } from "../config/constants.js";
import { logger } from "./logger.js";

interface CachedDeletion {
  filePath: string;
  content: string;
  timestamp: number;
  sessionId: string;
}

/**
 * Initialize cache directory
 */
async function ensureCacheDir(): Promise<void> {
  try {
    await mkdir(DELETION_CACHE_DIR, { recursive: true });
  } catch (error) {
    logger.error("Failed to create cache directory:", error);
  }
}

/**
 * Generate cache key from file path and session
 */
function getCacheKey(filePath: string, sessionId: string): string {
  // Use simple hash of filepath to avoid filesystem issues
  const hash = Buffer.from(filePath).toString("base64").replace(/[/+=]/g, "_");
  return `${sessionId}_${hash}.json`;
}

/**
 * Store file content before deletion
 */
export async function cacheFileForDeletion(
  filePath: string,
  content: string,
  sessionId: string,
): Promise<void> {
  try {
    await ensureCacheDir();

    const cached: CachedDeletion = {
      filePath,
      content,
      timestamp: Date.now(),
      sessionId,
    };

    const cacheKey = getCacheKey(filePath, sessionId);
    const cachePath = join(DELETION_CACHE_DIR, cacheKey);

    await writeFile(cachePath, JSON.stringify(cached, null, 2), "utf-8");
    logger.debug(`Cached file content: ${filePath} (${content.length} chars)`);
  } catch (error) {
    logger.error(`Failed to cache file for deletion: ${filePath}`, error);
  }
}

/**
 * Retrieve cached file content for diff generation
 * Removes the cache entry after retrieval
 */
export async function getCachedFileContent(
  filePath: string,
  sessionId: string,
): Promise<string | null> {
  try {
    const cacheKey = getCacheKey(filePath, sessionId);
    const cachePath = join(DELETION_CACHE_DIR, cacheKey);

    try {
      const content = await readFile(cachePath, "utf-8");
      const cached: CachedDeletion = JSON.parse(content);

      // Check if cache is still valid (not expired)
      const age = Date.now() - cached.timestamp;

      if (age > DELETION_CACHE_TTL_MS) {
        logger.debug(`Cache expired for ${filePath} (${age}ms old)`);
        await rm(cachePath).catch(() => {}); // Clean up expired cache
        return null;
      }

      // Clean up cache after retrieval
      await rm(cachePath).catch(() => {});

      logger.debug(`Retrieved cached content for ${filePath} (${cached.content.length} chars)`);
      return cached.content;
    } catch (readError) {
      // Cache file doesn't exist
      logger.debug(`Cache not found for ${filePath}`);
      return null;
    }
  } catch (error) {
    logger.error(`Failed to retrieve cached content: ${filePath}`, error);
    return null;
  }
}

/**
 * Clean up old cache entries
 * Called periodically to prevent cache buildup
 */
export async function cleanupOldCache(): Promise<void> {
  try {
    await ensureCacheDir();
    const files = await readdir(DELETION_CACHE_DIR);
    const now = Date.now();

    for (const file of files) {
      try {
        const filePath = join(DELETION_CACHE_DIR, file);
        const stats = await stat(filePath);

        // Delete files older than TTL
        const age = now - stats.mtimeMs;
        if (age > DELETION_CACHE_TTL_MS) {
          await rm(filePath);
          logger.debug(`Cleaned up old cache entry: ${file} (${age}ms old)`);
        }
      } catch (error) {
        // Ignore errors for individual files
        logger.debug(`Failed to clean up cache file ${file}:`, error);
      }
    }
  } catch (error) {
    logger.error("Failed to cleanup old cache:", error);
  }
}
