/**
 * Message parsing logic for Claude JSONL files
 */

import { readFile } from "node:fs/promises";
import type { ClaudeJSONLEntry, ToolUse } from "../types/extractors.js";
import type { ClaudeExtractedMessage } from "../types/index.js";
import { applyMessageFilter, restoreFilteringState } from "../utils/command-filters.js";
import { logger } from "../utils/logger.js";
import {
  extractTextContent,
  extractToolUse,
  extractToolUseResult,
  generateMessageId,
  logDiff,
} from "./extraction-utils.js";

/**
 * Extract only NEW messages and tool uses from JSONL file (incremental extraction)
 *
 * @param filePath - Path to conversation.jsonl file
 * @param sessionId - Session identifier
 * @param lastReadLine - Last line number that was read (0-indexed)
 * @returns New messages, tool uses, and updated line number
 */
export async function extractNewMessagesFromFile(
  filePath: string,
  sessionId: string,
  lastReadLine = 0,
): Promise<{
  messages: ClaudeExtractedMessage[];
  toolUses: ToolUse[];
  newLastReadLine: number;
  totalLines: number;
}> {
  const messages: ClaudeExtractedMessage[] = [];
  const toolUses: ToolUse[] = [];

  try {
    logger.debug(`Incremental extraction for ${sessionId}: reading from line ${lastReadLine}`);

    const content = await readFile(filePath, "utf-8");
    const lines = content.split("\n").filter((line) => line.trim());
    const totalLines = lines.length;

    // Check if there are new lines to process
    if (totalLines <= lastReadLine) {
      logger.debug(`No new lines for ${sessionId}: total=${totalLines}, lastRead=${lastReadLine}`);
      return { messages, toolUses, newLastReadLine: lastReadLine, totalLines };
    }

    // Process only new lines
    const newLines = lines.slice(lastReadLine);
    logger.info(
      `Processing ${newLines.length} new lines for session ${sessionId} (lines ${lastReadLine + 1}-${totalLines})`,
    );

    // Extract messages without worrying about message_index
    // (will be assigned sequentially during queuing)
    let tempMessageCounter = 0;

    // State flag: filter assistant responses after excluded user command
    // Initialize by checking the last user message from already-processed lines
    let filteringAssistantResponses = restoreFilteringState(lines, lastReadLine);

    for (let i = 0; i < newLines.length; i++) {
      const line = newLines[i];
      const lineNumber = lastReadLine + i;

      try {
        const entry: ClaudeJSONLEntry = JSON.parse(line);

        // Skip entries without message field
        if (!entry.message) continue;

        const role = entry.message.role;
        const content = entry.message.content;

        // Extract user or assistant messages
        if ((role === "user" || role === "assistant") && content) {
          const textContent = extractTextContent(content);

          if (textContent) {
            // Apply state-based filtering logic
            const filterResult = applyMessageFilter(role, textContent, filteringAssistantResponses);
            filteringAssistantResponses = filterResult.newState;

            if (filterResult.shouldFilter) {
              continue;
            }

            const messageId = entry.uuid || generateMessageId(sessionId, tempMessageCounter);

            messages.push({
              id: messageId,
              session_id: sessionId,
              role: role,
              content: textContent,
              created_at: entry.timestamp || new Date().toISOString(),
              message_index: tempMessageCounter, // Temporary - reassigned sequentially during queuing
            });
            tempMessageCounter++;

            logger.debug(
              `Extracted ${role} message at line ${lineNumber + 1}: ${textContent.substring(0, 50)}...`,
            );
          }
        }

        // Extract tool use results with diffs (primary source)
        // This contains the actual results and diffs after tool execution
        if (entry.toolUseResult) {
          const toolUseWithDiff = extractToolUseResult(entry, sessionId);
          if (toolUseWithDiff) {
            toolUses.push(toolUseWithDiff);

            logger.debug(
              `Extracted tool result at line ${lineNumber + 1}: ${toolUseWithDiff.file_path}`,
            );

            logDiff(toolUseWithDiff.file_path || "", toolUseWithDiff.diff);
          }
        }
        // Fallback: Check message content array for tool uses without results
        // This handles Bash commands and other tools that don't have toolUseResult
        else if (Array.isArray(content)) {
          for (const contentBlock of content) {
            if (contentBlock.type === "tool_use") {
              const extractedToolUses = await extractToolUse(
                contentBlock,
                sessionId,
                entry.timestamp,
              );
              for (const toolUse of extractedToolUses) {
                toolUses.push(toolUse);
                logger.debug(
                  `Extracted tool use at line ${lineNumber + 1}: ${toolUse.tool_name} on ${toolUse.file_path}`,
                );
              }
            }
          }
        }
      } catch (parseError) {
        logger.debug(
          `Failed to parse JSONL line ${lineNumber + 1}: ${line.substring(0, 100)}...`,
          parseError,
        );
      }
    }

    logger.info(
      `Incremental extraction complete: ${messages.length} messages, ${toolUses.length} tool uses`,
    );

    return {
      messages,
      toolUses,
      newLastReadLine: totalLines,
      totalLines,
    };
  } catch (error) {
    logger.error(`Failed to incrementally read conversation file ${filePath}:`, error);
    return {
      messages,
      toolUses,
      newLastReadLine: lastReadLine,
      totalLines: lastReadLine,
    };
  }
}
