/**
 * Diff utilities
 *
 * Functions for handling and sanitizing diff data
 */

import * as Diff from "diff";
import { MAX_DIFF_SIZE_BYTES } from "../config/constants.js";
import { logger } from "./logger.js";

/**
 * Create a unified diff format from old and new strings
 * Uses the same library as the VSCode extension
 */
export function createUnifiedDiff(filePath: string, oldString: string, newString: string): string {
  try {
    return Diff.createPatch(filePath, oldString.trimEnd(), newString.trimEnd(), "", "", {
      context: 3,
    });
  } catch (error) {
    logger.warn(`Failed to create unified diff for ${filePath}`, error);
    return "";
  }
}

/**
 * Sanitize diff data to prevent unbounded memory growth
 * Converts old_string/new_string to unified diff format to save space
 *
 * Handles three cases:
 * - File creation: old_string is empty/undefined, new_string has content
 * - File edit: both old_string and new_string have content
 * - File deletion: old_string has content, new_string is empty/undefined
 */
export function sanitizeDiff(
  diff: { old_string?: string; new_string?: string; structured_patch?: unknown } | undefined,
  filePath?: string,
): string | undefined {
  if (!diff || !filePath) {
    return undefined;
  }

  // Normalize strings - treat undefined/null as empty string
  const oldString = diff.old_string ?? "";
  const newString = diff.new_string ?? "";

  // If both strings are empty, there's no meaningful diff
  if (oldString === "" && newString === "") {
    return undefined;
  }

  const unifiedDiff = createUnifiedDiff(filePath, oldString, newString);

  if (!unifiedDiff) {
    return undefined;
  }

  const sizeBytes = Buffer.byteLength(unifiedDiff, "utf-8");

  if (sizeBytes > MAX_DIFF_SIZE_BYTES) {
    logger.warn(
      `Diff size ${sizeBytes} bytes exceeds limit of ${MAX_DIFF_SIZE_BYTES} bytes, skipping`,
    );
    return undefined;
  }

  return unifiedDiff;
}
