#!/usr/bin/env node

/**
 * CLI handler for PreToolUse events
 *
 * Called before a tool executes - used to cache file content before deletion
 * Usage: node dist/hooks/pre-tool-handler-cli.js
 */

import {
  cacheFilesForDeletion,
  findConversationFile,
  findRecentBashCommand,
  parseRmCommand,
} from "../utils/extraction-helpers.js";
import { logger } from "../utils/logger.js";
import { cleanupOldCache } from "../utils/deletion-cache.js";

async function main() {
  const startTime = Date.now();

  // Get environment variables from Claude
  const projectDir = process.env.CLAUDE_PROJECT_DIR;

  try {
    // Get session and conversation file
    if (!projectDir) {
      logger.warn("[PreToolUse] No project directory, cannot find session");
      process.exit(0);
    }

    const sessionInfo = await findConversationFile(projectDir);
    if (!sessionInfo) {
      logger.warn("[PreToolUse] Could not find session info");
      process.exit(0);
    }

    const { conversationFile, sessionId } = sessionInfo;

    const command = await findRecentBashCommand(conversationFile);
    if (!command) {
      logger.debug("No Bash command found in recent JSONL entries");
      process.exit(0);
    }

    const filesToDelete = parseRmCommand(command);
    if (filesToDelete.length === 0) {
      logger.debug("Not an rm command, skipping");
      process.exit(0);
    }

    logger.info(
      `Detected file deletion: ${filesToDelete.length} file(s) - ${filesToDelete.join(", ")}`,
    );

    // Cache content for each file to be deleted
    await cacheFilesForDeletion(filesToDelete, sessionId, projectDir);

    // Cleanup old cache entries (async, don't wait)
    cleanupOldCache().catch((err) => logger.debug("Cache cleanup failed:", err));

    const totalTime = Date.now() - startTime;
    logger.debug(`PreToolUse complete in ${totalTime}ms`);
  } catch (error) {
    logger.error("Failed to process PreToolUse:", error);
    // Exit with 0 to avoid disrupting Claude's operation
    process.exit(0);
  }
}

main().catch((error) => {
  console.error("PreToolUse handler error:", error);
  process.exit(1);
});
