/**
 * Utility functions for extracting data from Claude JSONL entries
 */

import { createHash } from "node:crypto";
import {
  MAX_CONTENT_PREVIEW_LENGTH,
  MAX_SESSION_TITLE_LENGTH,
  MIN_SESSION_TITLE_LENGTH,
} from "../config/constants.js";
import type { ClaudeJSONLEntry, ContentBlock, ToolInput, ToolUse } from "../types/extractors.js";
import { shouldExcludeCommand } from "../utils/command-filters.js";
import { getCachedFileContent } from "../utils/deletion-cache.js";
import { sanitizeDiff } from "../utils/diff-utils.js";
import { logger } from "../utils/logger.js";

/**
 * Extract text content from message content field
 */
export function extractTextContent(content: string | ContentBlock[]): string {
  if (typeof content === "string") {
    return content;
  }

  if (Array.isArray(content)) {
    const textBlocks = content
      .filter((block) => block.type === "text" && block.text)
      .map((block) => block.text);
    return textBlocks.join("\n");
  }

  return "";
}

/**
 * Parse Bash command to extract file paths from file operation commands
 * Handles: rm commands (including multiple files)
 * Returns array of operations (one per file)
 */
function parseBashCommand(command: string): Array<{ operation: string; filePath: string }> {
  if (!command) return [];

  const cmd = command.trim();

  // Match rm commands: rm, rm -rf, rm -f, etc.
  // Pattern: rm [options] filepath(s)
  const rmMatch = cmd.match(/^rm\s+(?:-[a-zA-Z]+\s+)*(.+)$/);
  if (rmMatch) {
    const pathsString = rmMatch[1].trim();

    // Split by spaces but respect quotes
    const paths =
      pathsString.match(/(?:[^\s"']+|"[^"]*"|'[^']*')+/g)?.map((p) => p.replace(/['"]/g, "")) || [];

    return paths.map((filePath) => ({ operation: "Delete", filePath }));
  }

  return [];
}

/**
 * Extract tool use information (file path, operation, etc.)
 * This captures tool_use content blocks from assistant messages
 * Returns an array to support multiple files in a single command (e.g., rm file1 file2)
 */
export async function extractToolUse(
  contentBlock: ContentBlock,
  sessionId: string,
  timestamp?: string,
): Promise<ToolUse[]> {
  try {
    const toolName = contentBlock.name;
    const input: ToolInput = contentBlock.input || {};

    if (!toolName) return [];

    // Extract file path from various tool formats
    let filePath: string | undefined;
    let content: string | undefined;
    let operationType: string = toolName; // Default to original tool name

    // Bash tool (shell commands including file operations)
    if (toolName === "Bash" || toolName === "bash" || toolName === "Shell") {
      const command = input.command || input.cmd;
      if (command) {
        // Filter out excluded commands (Claude Code and Zest plugin commands)
        if (shouldExcludeCommand(command)) {
          logger.debug(`Filtered out excluded bash command: ${command.substring(0, 50)}...`);
          return [];
        }

        const parsedOperations = parseBashCommand(command);
        if (parsedOperations.length > 0) {
          // Handle multiple file operations from a single Bash command
          const toolUses: ToolUse[] = [];

          for (const parsed of parsedOperations) {
            const toolUse: ToolUse = {
              session_id: sessionId,
              tool_name: parsed.operation,
              file_path: parsed.filePath,
              timestamp: timestamp || new Date().toISOString(),
            };

            // For Delete operations, try to retrieve cached file content
            if (parsed.operation === "Delete") {
              const cachedContent = await getCachedFileContent(parsed.filePath, sessionId);

              // Check if cache was found (null means not found, empty string is valid)
              if (cachedContent !== null) {
                toolUse.diff = sanitizeDiff(
                  { old_string: cachedContent, new_string: "" },
                  parsed.filePath,
                );
                logger.info(`Generated deletion diff for ${parsed.filePath}`);
              } else {
                logger.warn(
                  `No cached content found for ${parsed.filePath} - tracking without diff`,
                );
              }
            }

            toolUses.push(toolUse);
          }

          return toolUses;
        }
      }
    }
    // Write tool (file creation)
    else if (toolName === "Write" || toolName === "write") {
      filePath = input.file_path || input.path;
      content = input.content || input.contents;
    }
    // Edit tool (StrReplace in Claude Code)
    else if (toolName === "Edit" || toolName === "StrReplace" || toolName === "str_replace") {
      filePath = input.file_path || input.path;
    }
    // Delete tool (file deletion) - keeping for compatibility
    else if (toolName === "Delete" || toolName === "delete" || toolName === "rm") {
      filePath = input.file_path || input.path;
    }
    // EditNotebook tool
    else if (toolName === "EditNotebook" || toolName === "edit_notebook") {
      filePath = input.target_notebook || input.notebook_path;
    }
    // Read tool (also tracking reads for context)
    else if (toolName === "Read" || toolName === "read") {
      filePath = input.file_path || input.path;
    }

    if (!filePath) {
      // Try generic path fields
      filePath = input.path || input.file_path || input.filepath;
    }

    if (!filePath) {
      return [];
    }

    const toolUse: ToolUse = {
      session_id: sessionId,
      tool_name: operationType,
      file_path: filePath,
      content: content?.substring(0, MAX_CONTENT_PREVIEW_LENGTH),
      timestamp: timestamp || new Date().toISOString(),
    };

    // For Write operations (file creation), generate diff from content
    if ((operationType === "Write" || operationType === "write") && content) {
      toolUse.diff = sanitizeDiff({ old_string: "", new_string: content }, filePath);
    }

    return [toolUse];
  } catch (error) {
    logger.debug("Failed to extract tool use:", error);
    return [];
  }
}

/**
 * Determine tool name based on operation type
 * Maps Claude's toolUseResult.type to our standard tool names
 */
function getToolNameFromResultType(type?: string, oldString?: string, newString?: string): string {
  // Explicit type from Claude
  if (type === "create") return "Write";
  if (type === "delete") return "Delete";

  // Infer from content: if no old content but has new content, it's a create
  if (!oldString && newString) return "Write";

  // Infer from content: if has old content but no new content, it's a delete
  if (oldString && !newString) return "Delete";

  // Default to Edit for modifications
  return "Edit";
}

/**
 * Extract tool use result with code diffs
 *
 * Handles:
 * - File creation (type: "create", or oldString empty with newString present)
 * - File editing (type: "edit", or both oldString and newString present)
 * - File deletion (type: "delete", or oldString present with newString empty)
 */
export function extractToolUseResult(entry: ClaudeJSONLEntry, sessionId: string): ToolUse | null {
  try {
    const result = entry.toolUseResult;
    if (!result || !result.filePath) return null;

    const toolName = getToolNameFromResultType(result.type, result.oldString, result.newString);

    const toolUse: ToolUse = {
      session_id: sessionId,
      tool_name: toolName,
      file_path: result.filePath,
      timestamp: entry.timestamp || new Date().toISOString(),
    };

    // Add diff information if available
    // sanitizeDiff now handles file creation (empty old) and deletion (empty new)
    if (
      result.structuredPatch ||
      result.oldString !== undefined ||
      result.newString !== undefined
    ) {
      const rawDiff = {
        old_string: result.oldString,
        new_string: result.newString,
        structured_patch: result.structuredPatch,
      };
      toolUse.diff = sanitizeDiff(rawDiff, result.filePath);
    }

    return toolUse;
  } catch (error) {
    logger.debug("Failed to extract tool use result:", error);
    return null;
  }
}

/**
 * Extract session title from first user message
 */
export function extractSessionTitleFromContent(content: string): string | null {
  const lines = content.split("\n").filter((line) => line.trim());

  // Find first user message
  for (const line of lines) {
    try {
      const entry: ClaudeJSONLEntry = JSON.parse(line);
      if (entry.message?.role === "user" && entry.message.content) {
        const text = extractTextContent(entry.message.content);
        if (text && text.length >= MIN_SESSION_TITLE_LENGTH) {
          // Use first MAX_SESSION_TITLE_LENGTH characters as title
          return text.substring(0, MAX_SESSION_TITLE_LENGTH).trim();
        }
      }
    } catch {
      // Skip invalid lines
    }
  }

  return null;
}

/**
 * Generate a deterministic message ID
 */
export function generateMessageId(sessionId: string, messageIndex: number): string {
  const hash = createHash("sha256").update(`${sessionId}-${messageIndex}`).digest("hex");
  return `msg_${hash.substring(0, 16)}`;
}

/**
 * Log code diff information
 */
export function logDiff(filePath: string, diff?: string): void {
  if (!diff) return;

  logger.info(`Code change detected in ${filePath}:`);

  // Log first few lines of the unified diff
  const lines = diff.split("\n").slice(0, 10);
  for (const line of lines) {
    logger.info(`  ${line}`);
  }
  if (diff.split("\n").length > 10) {
    logger.info("  ...");
  }
}
